.container {
  position: relative;
  // height: 90vh;
  .jellyimg {
    position: absolute;
    width: 300px;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
  .container2 {
    padding: 30px 67px 30px 67px;
    height: 100%;

    .progress_bar {
      background: rgba(196, 196, 196, 0.5);
      border-radius: 10px;
      // width: 100%;
      height: 15px;
      .progress_complete {
        background: #6945ed;

        height: 100%;
        border-radius: 10px;
      }
    }
    .progress_percent {
      font-family: Circular Std;
      font-style: normal;
      font-weight: bold;
      font-size: 28px;
      line-height: 35px;
      display: flex;
      align-items: center;
      text-align: right;
      margin: 30px 5px;
      color: #7854f7;
    }
    .body {
      display: flex;
      justify-content: space-between;
      // grid-gap: 50px;
      padding: 50px 0 100px 0;
      .form {
        // background-color: pink;
        width: 30%;
      }
      .hardCopy {
        width: 60%;
        height: 78vh;
        background: #ffffff;
        position: relative;
        box-shadow: 0px 4px 50px rgba(120, 84, 247, 0.15);
        padding: 30px 20px;
        background-position: center;
        background-size: 70% 70%;
        background-repeat: no-repeat;
        // white-space: nowrap;
        .content {
          overflow: hidden;
          // text-overflow: ellipsis;
          height: 100%;
          // background-color: pink;
        }
      }
    }
  }
}
@media (min-width: 1281px) {
}

/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/

@media (min-width: 1025px) and (max-width: 1280px) {
}

/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {
  .container {
    .container2 {
      padding: 30px 50px;
    }
  }
}

/* 
  ##Device = Tablets, Ipads (landscape)
  ##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .container {
    .container2 {
      padding: 30px 50px;
    }
  }
}

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (min-width: 600px) and (max-width: 767px) {
  .container {
    overflow: hidden;
    .container2 {
      padding: 30px 50px;
      .body {
        flex-direction: column;
        grid-gap: 50px;
        padding-bottom: 50px;
        .form {
          width: 60%;
          margin: auto;
        }
        .hardCopy {
          width: 80%;
          margin: auto;
        }
      }
    }
    .jellyimg {
      // bottom: 70px;
      right: -65px;
    }
  }
}
@media (min-width: 481px) and (max-width: 599px) {
  .container {
    overflow: hidden;
    .container2 {
      padding: 30px 50px;
      .body {
        flex-direction: column;
        grid-gap: 50px;
        padding-bottom: 50px;
        .form {
          width: 80%;
          margin: auto;
        }
        .hardCopy {
          width: 90%;
          margin: auto;
        }
      }
    }
    .jellyimg {
      right: -65px;
    }
  }
}

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
  .container {
    overflow: hidden;
    .container2 {
      padding: 30px 30px;

      .progress_percent {
        font-size: 22px;
        margin: 10px 5px;
      }
      .body {
        flex-direction: column;
        grid-gap: 50px;
        padding-bottom: 50px;
        .form {
          width: 100%;
          margin: auto;
        }
        .hardCopy {
          width: 100%;
          margin: auto;
        }
      }
    }
    .jellyimg {
      right: -65px;
    }
  }
}
