.box {
  position: relative;
  background: #ffffff;
  border-radius: 17px;
  padding: 30px 40px;
  // width: 350px;
  display: flex;
  // justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 90vh;
  max-width: 90%;
  h5 {
    font-family: Circular Std;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 0 30px 0;
    color: #17141f;
  }
  .close {
    position: absolute;
    top: 10px;
    right: 12px;
    cursor: pointer;
  }
}
.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 300px;
  h3 {
    text-align: center;
    margin-bottom: 0;
    font-family: Circular Std;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #17141f;
    width: 80%;
  }
  p {
    font-family: Circular Std;
    font-style: normal;
    font-weight: 450;
    font-size: 16px;
    line-height: 21px;
    /* or 133% */

    display: flex;
    align-items: center;
    text-align: center;

    color: rgba(23, 20, 31, 0.8);
  }
}
.manageAdons {
  // width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h3 {
    text-align: center;
    margin-bottom: 0;
    font-family: Circular Std;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #17141f;
    width: 80%;
  }

  .ad_ons {
    margin: 20px 0 10px 0;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    width: 100%;
    .ad_on {
      display: flex;
      flex-direction: column;
      grid-gap: 10px;
      cursor: pointer;
      .box {
        background: #f1f0f5;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 18px 25px;
        h3 {
          font-family: Circular Std;
          font-style: normal;
          font-weight: bold;
          font-size: 16px;
          line-height: 23px;
          /* identical to box height */
          margin: 0;
          color: #1b2125;
        }
        img {
          right: 0;
          transform: rotate(-92deg);
          width: 13px;
        }
      }
      .card {
        display: flex;
        // background: #f1f0f5;
        background: #ffffff;
        border: 1px solid #8692a6;
        box-sizing: border-box;
        border-radius: 6px;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 5px 17px;
        cursor: pointer;
        .text {
          width: 100%;
          .top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            h2 {
              font-family: Circular Std;
              font-style: normal;
              font-weight: bold;
              font-size: 16px;
              line-height: 30px;
              /* identical to box height, or 154% */

              letter-spacing: -0.333333px;
              margin: 0;
              color: #000000;
            }
          }
        }
        p {
          font-family: Circular Std;
          font-style: normal;
          font-weight: 450;
          font-size: 14px;
          line-height: 32px;
          /* identical to box height, or 202% */

          letter-spacing: -0.333333px;
          margin: -3px 0 0 0;
          color: rgba(23, 20, 31, 0.8);
        }

        // checkbox
        .container2 {
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          position: relative;
          padding-left: 35px;
          margin-bottom: 12px;
          cursor: pointer;
          font-size: 22px;

          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
        }
        input {
          position: absolute;
          opacity: 0;
          cursor: pointer;
        }
        .checkmark {
          position: absolute;
          top: 5px;
          left: 0;
          height: 20px;
          width: 20px;
          // background-color: #eee;
          border: 3px solid #979797;
          border-radius: 50%;
        }
        &:hover input ~ .checkmark {
          // border: 3px solid #7854f7;
        }
        &:hover {
          // border: 3px solid #7854f7;
        }
        .container2 input:checked ~ .checkmark {
          border: 3px solid #7854f7;
        }
        .checkmark:after {
          content: '';
          position: absolute;
          display: none;
        }
        .container2 input:checked ~ .checkmark:after {
          display: block;
        }
        .checkmark:after {
          top: 2px;
          left: 2px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #7854f7;
        }
        //  checkbox
      }
      .activeCard {
        border: 1px solid #7854f7;
      }
    }
  }
}
.buttons {
  width: 100%;
  margin: 10px 0;
  display: flex;
  justify-content: space-evenly;
  grid-gap: 20px;
  button {
    font-family: Circular Std;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    /* identical to box height */
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    color: #ffffff;
    border: none;
    outline: none;
    padding: 15px;
    text-align: center;
    cursor: pointer;

    &:first-of-type {
      flex: 1;
      background: #808191;
      border-radius: 5px;
    }
    &:last-of-type {
      flex: 2;
      background: #ef4358;
      box-shadow: 0px 8px 14px rgba(239, 67, 88, 0.5);
      border-radius: 5px;
    }
  }
}
.greenButtons {
  width: 100%;
  margin: 10px 0;
  display: flex;
  justify-content: center;
  grid-gap: 20px;
  button {
    font-family: Circular Std;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    /* identical to box height */
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    color: #ffffff;
    border: none;
    outline: none;
    padding: 15px;
    text-align: center;
    cursor: pointer;

    &:first-of-type {
      flex: 1/2;
      background: #808191;
      border-radius: 5px;
    }
    &:last-of-type {
      flex: 1/2;
      background: #00d339;
