      border-radius: 5px;
    }
  }
}
.edit {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .form {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 0;
    label {
      margin-bottom: 0;
      font-family: Inter;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      display: flex;
      align-items: center;
      color: #696f79;
    }
    input {
      background: #ffffff;
      border: 1px solid #7854f7;
      box-sizing: border-box;
      border-radius: 6px;
      font-family: Circular Std;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      display: flex;
      align-items: center;
      margin: 10px 0;
      color: #000000;
    }
  }
}
