/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
  .container {
    .subscription {
      .membership {
        .top {
          flex-direction: column-reverse;
          align-items: flex-start;
          h1 {
            width: 80%;
            margin-top: -35px;
            font-size: 24px;
            line-height: 35px;
          }
          .right {
            width: 100%;
            h3 {
              font-size: 24px;
              line-height: 35px;
            }
            p {
              display: none;
            }
          }
        }
        .bottom {
          flex-direction: column;
          align-items: flex-start;
          grid-gap: 20px;
          .button,
          .left {
            width: 100%;
            p {
              font-size: 14px;
            }
            button {
              font-size: 11px;
              float: unset;
            }
          }
        }
      }
      .date {
        div {
          p {
            font-size: 14px;
            line-height: 20px;
          }
          h1 {
            font-size: 24px;
            line-height: 35px;
          }
        }
        .button {
          button {
            font-size: 11px;
          }
        }
      }
    }
    .invoices {
      .box {
        padding: 0 10px;
        .left {
          p {
          }
          h5 {
            font-size: 11px;

            p {
              display: unset;
              font-size: 10px;
              span {
                font-size: 10px;
              }
            }
          }
        }
        .center {
          display: none;
        }
      }
    }
  }
}
