.form {
  width: 100%;
  height: 100%;
  // padding: 0 150px;
  form {
    justify-content: center;
    // display: flex;
    width: 60%;
    margin: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    // display: block;

    align-content: center;
    h3 {
      font-family: Circular Std;
      font-style: normal;
      font-weight: bold;
      font-size: 23px;
      line-height: 38px;
      /* identical to box height */

      display: flex;
      align-items: center;
      margin: 0;

      color: #17141f;
    }
    p {
      font-family: Circular Std;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      width: 300px;
      line-height: 28px;
      /* or 156% */
      margin: 0;
      margin-bottom: 30px;
      display: flex;
      align-items: center;

      color: #8692a6;
    }
    label {
      font-family: Circular Std;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      display: flex;
      align-items: center;
      margin: 15px 0;
      color: #696f79;
    }
    input {
      border: 1px solid #8692a6;
      box-sizing: border-box;
      border-radius: 6px;
      font-family: Circular Std;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      // line-height: 18px;
      /* identical to box height */
      height: 50px;
      padding: 0 25px;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      color: #8692a6;
      background: #ffffff;
      box-sizing: border-box;
      border-radius: 6px;
      outline: none;
      &:focus {
        box-shadow: 0px 4px 10px 3px rgba(0, 0, 0, 0.11);
        border: 1px solid #1565d8;
      }
    }
    input[type='submit'] {
      background: #7854f7;
      margin-top: 50px;
      border-radius: 6px;
      color: white;
      display: flex;
      //   height: unset;
      cursor: pointer;

      padding: unset;
      justify-content: center;
      align-content: center;
      font-family: Circular Std;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      //   line-height: 27px;
      /* identical to box height */

      color: #ffffff;
    }
    div {
      font-family: Inter;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 15px;
      /* identical to box height */

      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;
      color: #8692a6;
    }
  }
}
